import React,{useState} from 'react';
import './comment.css'
import { Link as RRDLink } from "react-router-dom";
import { Routes } from '../../Routes'
import Button from '@material-ui/core/Button';
import { Link,Typography } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import { observer } from 'mobx-react';
import TextField from '@material-ui/core/TextField';
import TimeAgo from 'timeago-react';
import {register} from 'timeago.js';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ru from 'timeago.js/lib/lang/ru';
register('ru', ru);

const CommentLvl = observer(({comment,setVote}) => {
  const vote = comment.usersRated[0]?comment.usersRated[0].value:0
  return (
    <div style={{position: 'absolute', right: 8,marginTop: -10}}>
    <IconButton aria-label="delete" size="medium" onClick={()=>setVote({commentId:comment.id,value:vote===1?0:1,comment})}>
      <KeyboardArrowUpIcon color={vote>0?'secondary':'inherit'} style={{paddingTop:1}} fontSize="inherit" />
    </IconButton>
      <Typography color={comment.value===0?"textSecondary":comment.value>0?"secondary":"error"} variant="body1" component="span" >
        {comment.value}
      </Typography>
    <IconButton aria-label="delete"  size="medium" onClick={()=>setVote({commentId:comment.id,value:vote===-1?0:-1,comment})}>
      <KeyboardArrowDownIcon color={vote<0?'error':'inherit'} style={{paddingBottom:1}} fontSize="inherit" />
    </IconButton>
    </div>
  )
})
const Comment = observer(({comment,mangaId,setComment,adjacencyList,setVote}) =>{ 
  const [reply,setReply] = useState(false)
  return(
      <div class="comment">
        <RRDLink to={Routes.User.link(comment.user.id)} class="avatar">
          <img src={comment.user.imgUrl}></img>
        </RRDLink>
        <div class="content">
          <CommentLvl setVote={setVote} comment={comment}/>
          {/* <RRDLink to={Routes.User.link(comment.user.id)} class="author">{comment.user.username}</RRDLink> */}
          <Typography color="textPrimary" component={RRDLink} to={Routes.User.link(comment.user.id)} >{comment.user.username}</Typography>
          <Typography component={TimeAgo} locale='ru' datetime={comment.createdAt} color="textSecondary" className="metadata"></Typography>
          <Typography color="textSecondary" variant="body1" component="h4" >
            {

              comment.banStatus===0
              ?comment.comment
              :<span style={{color: 'rgba(192, 51, 51, 0.73)'}}> </span>
            }
            
          </Typography>
          {
            comment.rootPath.length<4&&
            <div class="actions">
              <Link component="button" onClick={()=>setReply(!reply)}>
                {reply?"":""}
              </Link>
            </div>
          }
        </div>
        { reply&&         
          <form onSubmit={e=>{e.preventDefault(); setComment({mangaId,rootId:comment.id,comment:e.target.comment.value}).then(()=>setReply(false))}} class="ui reply form">
            <div class="field">
              <TextField
                variant="outlined"
                margin="normal"
                fullWidth
                id="comment"
                label=''
                name="comment"
                autoComplete="comment"
                autoFocus
                multiline
                rows={5}  
                rowsMax={5}
              />
              <div style={{marginTop: -48, right: 12, position:'absolute'}}>
                <Button type="submit" variant="outlined" size="small" color="secondary">
                  
                </Button>
              </div>
            </div>
          </form>
        }
        {adjacencyList[comment.id]&&<div class="comments">{adjacencyList[comment.id].map(comment => <Comment setVote={setVote} setComment={setComment} adjacencyList={adjacencyList} mangaId={mangaId} comment={comment}/>)}</div>}
      </div>
)})

export default observer(({mangaId,commentStore}) => {
  const {adjacencyList,setComment,setVote} = commentStore
  const [text,setText] = useState('')
  return(
    <div class="ui threaded comments" style={{maxWidth:'100%',}}>
      <div class="comment" >
      <ListSubheader component="div" style={{height:24}}>
        
      </ListSubheader>
      <form onSubmit={e=>{e.preventDefault(); setComment({mangaId,comment:e.target.comment.value}).then(()=>setText(''))}} class="ui reply form">
            <div class="field">
              <TextField
                value={text}
                onChange={(e)=>setText(e.target.value)}
                variant="outlined"
                margin="normal"
                fullWidth
                id="comment"
                label=''
                name="comment"
                autoComplete="comment"
                autoFocus
                multiline
                rows={5}
                rowsMax={5}
              />
              <div style={{marginTop: -48, right: 12, position:'absolute'}}>
                <Button type="submit" variant="outlined" size="small" color="secondary">
                  
                </Button>
              </div>
            </div>
          </form>
        </div>
        <div className='comment-box'>
          {
            adjacencyList[0].map(comment=>{
              return <Comment setVote={setVote} setComment={setComment} adjacencyList={adjacencyList} mangaId={mangaId} comment={comment}/>
            })
          }
        </div>
    </div>      
  )
})

















